<template>
  <el-form ref="form" :model="form" label-width="80px">
    <el-form-item label="用户名">
      <el-input v-model="form.mail"></el-input>
    </el-form-item>
    <el-form-item label="密码">
      <el-input v-model="form.passwd"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" v-on:keyup.enter="login(form.mail,form.passwd)" @click="login(form.mail,form.passwd)">登陆</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    data () {
      return {
        logining: false,
        form: {
          mail: '',
          passwd: ''
        },
        checked: true
      }
    },
    methods: {
      login (mail, passwd) {
        this.axios.post('/login', {
          mail: mail,
          passwd: passwd
        }).then(function (response) {
          if (String(response.data) === '1') {
            location.href = '#/info'//  this.showDomains()
          } else {
            console.log(response.data)
          }
        })
      }
    }
  }

</script>



