import Vue from 'vue'
import Router from 'vue-router'
import domains from '@/components/domains'
import login from '@/components/login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/info',
      name: 'domains',
      component: domains
    },
    {
      path: '/',
      name: 'login',
      component: login
    }

  ]
})



