<template>
  <div>
    <el-tag type="primary">{{mydomain}}</el-tag>
    <!--<el-button @click.native="dialogFormlogin = true" type="primary">登录</el-button>-->
    <el-button type="primary">修改密码</el-button>
    <el-button type="primary">清空记录</el-button>
    <el-button @click.native="logout" type="primary">退出</el-button>
    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="domain"
        label="domain">
      </el-table-column>
      <el-table-column
        prop="http"
        label="http">
        <template scope="scope">
            <el-button
              size="large"
              @click="showHttp(scope.row.hash)">{{ "http："+scope.row.http }}</el-button>
            <el-button
              size="small"
              type="danger"
          @click="handleDelete(0,scope.hash)">删除</el-button>
      </template>
      </el-table-column>
      <el-table-column
        prop="dns"
        label="dns">
        <template scope="scope">
            <el-button
              size="large"
              @click="showDns(scope.row.hash)">{{ "dns："+scope.row.dns }}</el-button>
            <el-button
              size="small"
              type="danger"
              @click="handleDelete(1,scope.hash)">删除</el-button>
         </template>
      </el-table-column>
      <el-table-column
        prop="time"
        label="time">
      </el-table-column>
    </el-table>
    <textarea rows="25" cols="80" readonly="readonly" style="width:100%;overflow-x:hidden;overflow-y:auto;">{{reqlog}}</textarea>
    <!--登录对话框
    <el-dialog title="登录" :visible.sync="dialogFormlogin">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="用户名">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="form.passwd"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="login(form.name,form.passwd)">登陆</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    -->
  </div>
</template>

<script>
  export default {
    data () {
      return {
        tableData: [{
          hash: '',
          domain: '',
          http: '',
          dns: '',
          time: ''
        }],
        form: {
          name: '',
          passwd: ''
        },
        reqlog: 'log info',
        dialogFormlogin: false,
        mydomain: 'sub domain'
      }
    },
    created: function () {
      // console.log('info')
      this.axios.get('/getDomain').then((response) => {
        this.tableData = JSON.parse(response.data)
      })
    },
    methods: {
      showHttp (hashdomain) {
        console.log(hashdomain)
        this.dialogFormlogin = true
        this.axios.get('/showHttp/' + hashdomain).then((response) => {
          this.reqlog = response.data
        })

        // this.reqlog = 'change http'
      },
      showDns (hashdomain) {
        console.log(hashdomain)
        this.axios.get('/showDns/' + hashdomain).then((response) => {
          this.reqlog = response.data
        })
        // this.reqlog = 'change dns'
      },
      getDomain () {
        this.axios.get('/getDomain').then((response) => {
          this.reqlog = response.data
        })
      },
      showDomains () {
        // console.log('写domain')
        return [{
          hash: 'hashdomain',
          domain: '2016-05-02',
          http: '1',
          dns: '1',
          time: '111'
        }, {
          hash: 'hashdomain',
          domain: '2016-05-03',
          http: '2',
          dns: '2',
          time: '1111'
        }]
      },
      handleDelete (flag, hashdomain) {
        console.log('哈哈哈 没写 不写了')
      },
      logout () {
        var name = 'prefix'
        var exp = new Date()
        exp.setTime(exp.getTime() - 1)
        document.cookie = name + '= 1' + ';expires=' + exp.toGMTString()
      }
    }
  }
</script>



